<template>
  <div class="showContent detailContent">
    <requestAndHisSubMenu :routekey="routekey"></requestAndHisSubMenu>
    <backSubMenu :backTitle="backTitle" :lineUnder="false"></backSubMenu>
    <div class="detail">
      <div class="left">
        <div class="documentPart">
          <div>
            <span>{{documentType}}</span>
            <span>The Result of Document match</span>
          </div>
          <div>
            <img src="../../assets/image/document/ID IDCARD.png" alt>
          </div>
        </div>
        <div class="box_line"></div>
        <div class="box_info">
          <div class="box_icon">
            <img src="../../assets/image/cup.png" alt>
          </div>
          <label>BRAVO!</label>
          <label>The accuracy of this test has reached to {{percentNum}}</label>
        </div>
      </div>
      <div class="right">
        <div class="infor_form_detail">
          <div>
            <!-- Time -->
            <label>Time</label>
            <input type="text" :value="$util.timestampToDay(record.created_at)" readonly>
          </div>
          <!-- <div>
            <label>Event</label>
            <input type="text" :value="detailData.name" readonly>
          </div>-->
          <div class="hasDot">
            <!-- Situation -->
            <label>Situation</label>
            <input type="text" :value="record.status" readonly>
            <span :class="record.status=='success'?'greenDot':'redDot'" id="statusDot">
              <i class="iconfont amd-dot"></i>
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import requestAndHisSubMenu from "@/components/requestAndHisSubMenu";
import { mapGetters } from "vuex";
import backSubMenu from "@/components/backSubMenu";
export default {
  data() {
    return {
      routekey: ["documentMatchRequest", "documentMatchHistory"],
      loading: false,
      resultData: "",
      record: "",
      backTitle:"Detail",
      documentType:'',
      percentNum:'',
    };
  },
  components: {
    requestAndHisSubMenu,
    backSubMenu
  },
  computed: {
    ...mapGetters(["apiHistoryList"])
  },
  methods: {
    getDetailData() {
      let params = this.$route.params;
      console.log(params);
      this.record = params.record;
      this.resultData = params.record.response.data;
      this.documentType = this.resultData.label.toUpperCase();
      this.percentNum = this.resultData.score * 100 + "%";
    }
  },
  created() {
    if (this.$route.params.record) {
      this.getDetailData();
    } else {
      this.$router.go(-1);
    }
  }
};
</script>
<style lang="less">
@import "../../css/page.less";
</style>




